@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");

$frameColor: #fff;
$winIndicatorColor: #fff;
$middleColor: #fff;

$pink: #ff66ac;
$blue: #55e7f4;

body {
  text-align: center;
  margin: auto;
  background-color: tomato;
  transition: color 2s ease-in-out;
}

body.final {
  background-color: $blue;
}

.main-container {
  height: 700px;
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;

  @media only screen and (min-width: 600px) {
    height: auto;
  }
}

h1 {
  color: #fff;
  font-family: "Pacifico", cursive;
  font-size: 30px;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  text-shadow: 1px 1px 2px rgb(0, 0, 0);
  display: block;
  height: 80px;
  white-space: nowrap;

  @media only screen and (min-width: 600px) {
    font-size: 55px;
  }

  > span {
    transition: all 2s ease-in-out;
    position: relative;
    left: 0;
    top: 0;

    &.or {
      position: relative;
      top: 30px;
      font-size: 35px;
    }
  }
}

body.final h1 {
  .boy {
    top: calc(50vh - 60px);
    left: calc(50vw - 100px);

    @media only screen and (min-width: 600px) {
      left: calc(10vw);
    }
  }

  .or {
    left: 200px;
    opacity: 0;
  }

  .girl {
    left: 200px;
    opacity: 0;
  }
}

.luckywheel-container {
  max-width: 550px;
  min-width: 100px;
  width: 100%;
  position: relative;
  top: -50px;

  @media only screen and (min-width: 600px) {
    width: 50%;
    top: 0;
  }
}

.sectors .sector:nth-child(even) {
  fill: $blue;
}

.sectors .sector:nth-child(odd) {
  transition: all 0.5s ease-in-out;
  fill: $pink;
}

body.final .sectors .sector:nth-child(odd) {
  fill: $blue;
}

.frame,
.sticks {
  fill: $frameColor;
}

.winIndicator {
  fill: $winIndicatorColor;
}

.wheelMiddle {
  fill: $middleColor;
}

.luckywheel {
  margin: 40px auto;
  width: 85%;
  position: relative;
  top: 0;
  transition: all 2s ease-in-out;
}

body.final .luckywheel {
  top: 300px;
  opacity: 0;
}

.button-container {
  position: relative;
  top: -130px;

  @media only screen and (min-width: 600px) {
    top: -20px;
  }
}

/* CSS */
.button-28 {
  appearance: none;

  background-color: #fff;
  border: 2px solid #1a1a1a;
  border-radius: 15px;
  box-sizing: border-box;
  color: #3b3b3b;
  cursor: pointer;
  display: inline-block;
  font-family: Roobert, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  margin: 0;
  min-height: 60px;
  min-width: 0;
  outline: none;
  padding: 16px 24px;
  text-align: center;
  text-decoration: none;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 100%;
  will-change: transform;
}

.button-28.hide {
  opacity: 0;
}

.button-28:disabled {
  pointer-events: none;
}

.button-28:hover {
  color: #fff;
  background-color: #1a1a1a;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

.button-28:active {
  box-shadow: none;
  transform: translateY(0);
}

#confetti {
  display: none;
}

body.final #confetti {
  display: block;
  height: 100%;
  left: 0px;
  position: fixed;
  top: 0px;
  width: 100%;
  z-index: -1;
}
